import React, { Suspense } from "react";
import { Card, OverlayTrigger, Tooltip } from "react-bootstrap";
import PercentDifference from "./percentDifference";
import { kFormatter } from "../../../../../utils";

const TopcardSkeleton = React.lazy(() =>
  import("../skeleton/topcardSkeleton")
);

const TopCard = (props) => {

  const { res, col, loader, data } = props;

  return (
    <>
      {loader ? (
       <Suspense fallback={<></>}>
          <TopcardSkeleton res={res} col={col} />
        </Suspense> 
      ) : data && Object.keys(data).length ? (
        data.map((e, index) => {
          return (
            <div className={`col-sm-6 col-md-6 ${`col-xxl-${res}`}  mb-5`}>
              <Card className={`card-border-top top-border-${index} p-5 card-shadow`}>
                <span class="font-weight-bold font-size-h6 mb-2 text-secondary">
                  {e.name}
                  <OverlayTrigger
                    placement="top"
                    overlay={
                      <Tooltip>{e.tool_tip ? e.tool_tip : e.name}</Tooltip>
                    }
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="18"
                      height="18"
                      viewBox="0 0 24 24"
                      className="ms-1"
                    >
                      <g
                        id="Group_5749"
                        data-name="Group 5749"
                        transform="translate(12215 -4805)"
                      >
                        <rect
                          id="Rectangle_2065"
                          data-name="Rectangle 2065"
                          width="24"
                          height="24"
                          transform="translate(-12215 4805)"
                          fill="none"
                          opacity="0"
                        ></rect>
                        <path
                          id="Path_6691"
                          data-name="Path 6691"
                          d="M20,11a9,9,0,1,0,9,9A9,9,0,0,0,20,11Zm.923,12.692a.923.923,0,0,1-1.846,0V19.308a.923.923,0,1,1,1.846,0ZM20,17.231a.923.923,0,1,1,.923-.923A.923.923,0,0,1,20,17.231Z"
                          transform="translate(-12223 4797)"
                          fill={`#959595`}
                          opacity="0.999"
                        ></path>
                      </g>
                    </svg>
                  </OverlayTrigger>
                </span>
                <div className="d-flex align-items-center">
                  <span class="font-weight-boldest text-dark display-6 me-2">
                    { e.name.includes('Total Posts') ? kFormatter(Math.round(e.count)) : e.count }{ e.name.includes('Recommendation') || e.name.includes('CTR') ? '%' : ''}
                  </span>
                
                   { e.difference ?  
                   <PercentDifference difference={e.difference} flag={e.flag} /> : null }
                </div>
              </Card>
            </div>
          );
        })
      ) : null}
    </>
  );
};

export default TopCard;
