    import React from "react";
import { kFormatter } from "../../../../../utils";

    const PercentDifference = (props) => {

    return props.difference>0 ? (
        <span
        className={`score-count text-${
            props.flag == 1 ? "success" : "danger"
        } d-flex align-items-center ms-2`}
        >
  
        {props.flag == 1 ? (
            <svg
            class="me-1"
            width="18"
            height="16"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            fill="currentColor"
            >
            <path d="M12 8L18 14H6L12 8Z" fill={`#02a100`}></path>
            </svg>
        ) : (
            <svg
            className="me-1"
            width="12"
            height="12"
            viewBox="0 0 6 4"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            >
            <g clip-path="url(#clip0_1717_12)">
                <path d="M3 3.5L0 0.5H6L3 3.5Z" fill={`#f64e60`} />
            </g>
            </svg>
        )}

         
        {`${kFormatter(Math.round(props.difference))}%`}
        </span>
    ) : null;
    };

    export default React.memo(PercentDifference);
